// Sortable & Nestable Demo
// =============================================================

const sortNestDemo = {

  init () {

    this.bindUIActions()

  },

  bindUIActions () {

    // event handlers
    this.handleSortable()
    this.handleNestable()
  },

  byId (id) {
    return document.getElementById(id);
  },

  handleSortable () {
    const $this = this
    // Multi groups
    Sortable.create($this.byId('sortablemulti'), {
      animation: 150,
      draggable: '.sortable-tile',
      handle: '.drag-handle'
    });

    [].forEach.call($this.byId('sortablemulti').querySelectorAll('.sortable-lists'), function (el) {
      Sortable.create(el, {
        group: 'pages',
        animation: 150
      });
    });
  },

  handleNestable () {
    let updateOutput = function (e) {
      let list = e.length ? e : $(e.target);

      if (window.JSON) {
        $('#nestableOutput').text(window.JSON.stringify(list.nestable('serialize')));
      } else {
        $('#nestableOutput').text('JSON browser support required for this demo.');
      }
    }

    $('#nestable01').nestable({
      group: 1,
      maxDepth: 5
    }).on('change', updateOutput)

    $('#nestable02').nestable({
      group: 1,
      maxDepth: 5
    }).on('change', updateOutput)
  }
}

sortNestDemo.init()
